<template>
	<div id="ag-canvas">
		<div class="ag-btn-group">
			<span 
				@click="handleExit"
				class="ag-btn exitBtn"
				:class="{'disabled': !readyState}"
				title="Exit">
				<i class="ag-icon ag-icon-leave"></i>
			</span>
			<span v-if="attendeeMode === 'video'"
				@click="handleCamera"
				class="ag-btn videoControlBtn" 
				title="Enable/Disable Video">
				<i class="ag-icon ag-icon-camera"></i>
				<i class="ag-icon ag-icon-camera-off"></i>
			</span>
			<span v-if="attendeeMode !== 'audience'"
				@click="handleMic"
				class="ag-btn audioControlBtn" 
				title="Enable/Disable Audio">
				<i class="ag-icon ag-icon-mic"></i>
				<i class="ag-icon ag-icon-mic-off"></i>
			</span>
			<span
				class= "ag-btn ChatWindow"
				@click="handleChat"
				title="Show/Hide Chat">
				<i class="far fa-comment" aria-hidden="true"></i>
			</span>
			<span 
				@click="switchDisplay"
				class="ag-btn displayModeBtn"
				:class="{'disabled': streamList.length === 1 ||  streamList.length === 3 }"
				title="Switch Display Mode">
				<i class="ag-icon ag-icon-switch-display"></i>
			</span>
			<span 
				class= "ag-btn disableRemoteBtn"
				:class="{'disabled': streamList.length === 1 }"
				@click="hideRemote"
				title="Hide Remote Stream">
				<i class="ag-icon ag-icon-remove-pip"></i>
			</span>
			<span 
				v-if="displayMode !=='share'"
				class= "ag-btn screenShare"
				:class="{'disabled': streamList.length === 1 || streamList.length === 3 }"
				@click="handleScrenShare"
				title="Screen Share">
				<i class="ag-icon ag-icon-screen-share"></i>
			</span>
			<span 
				v-if="displayMode =='share'"
				@click = "handleRemoveScrenShare"
				class= "ag-btn disablescreenShare"
				title="Stop Screen Share">
				<i  class="ag-icon ag-icon-invalid"></i>
			</span>
			<!-- <span 
				v-if="!showProfile"
				class= "ag-btn ShowInfo"
				title="Show Profile Info"
				@click="handleShowInfo"
				>

				<i class="fa fa-toggle-on" aria-hidden="true"></i>
			</span>
			<span
				@click="handleHideInfo"
				v-if="showProfile"
				class= "ag-btn HideInfo"
				title="Hidden Profile Info">
				<i class="fa fa-toggle-off" aria-hidden="true"></i>
			</span> -->
			
			
		</div>
	</div>
</template>

<script>
import { merge } from "lodash";
import AgoraRTC from 'agora-rtc-sdk';
import * as Cookies from "js-cookie";

const tile_canvas = {
  "1": ["span 12/span 24"],
  "2": ["span 12/span 12/13/25", "span 12/span 12/13/13"],
  "3": ["span 6/span 12", "span 6/span 12", "span 6/span 12/7/19"],
  "4": [
    "span 6/span 12",
    "span 6/span 12",
    "span 6/span 12",
    "span 6/span 12/7/13"
  ],
  "5": [
    "span 3/span 4/13/9",
    "span 3/span 4/13/13",
    "span 3/span 4/13/17",
    "span 3/span 4/13/21",
    "span 9/span 16/10/21"
  ],
  "6": [
    "span 3/span 4/13/7",
    "span 3/span 4/13/11",
    "span 3/span 4/13/15",
    "span 3/span 4/13/19",
    "span 3/span 4/13/23",
    "span 9/span 16/10/21"
  ],
  "7": [
    "span 3/span 4/13/5",
    "span 3/span 4/13/9",
    "span 3/span 4/13/13",
    "span 3/span 4/13/17",
    "span 3/span 4/13/21",
    "span 3/span 4/13/25",
    "span 9/span 16/10/21"
  ]
};

export default {
	data() {
		return {
			client: {},
			localStream: {},
			shareClient: {},
			shareStream: {},
			shareScreenStatus : false,
			displayMode: "pip",
			streamList: [],
			readyState: false,
			intervalLocal:undefined,
			intevalRemote:undefined,
			showProfile:false,

		};
	},

  	props: [
		"transcode",
		"attendeeMode",
		"videoProfile",
		"channel",
		"baseMode",
		"appId",
		"uid"
  	],

  methods: {
		streamInit(uid, attendeeMode, videoProfile, config) {
			let defaultConfig = {
				streamID: uid,
				audio: true,
				video: true,
				screen: false
			};

			switch (attendeeMode) {
				case "audio-only":
					defaultConfig.video = false;
				break;
				case "audience":
					defaultConfig.video = false;
					defaultConfig.audio = false;
				break;
				default:
				case "video":
				break;
			}

			let stream = AgoraRTC.createStream(merge(defaultConfig, config));
			stream.setVideoProfile(videoProfile);
			return stream;
		},

		subscribeStreamEvents() {
			let rt = this;
			rt.client.on("stream-added", function(evt) {
				let stream = evt.stream;
				console.log("New stream added: " + stream.getId());
				console.log("At " + new Date().toLocaleTimeString());
				console.log("Subscribe ", stream);
				rt.client.subscribe(stream, function(err) {
					console.log("Subscribe stream failed", err);
				});
			});

			rt.client.on("peer-leave", function(evt) {
				console.log("Peer has left: " + evt.uid);
				console.log(new Date().toLocaleTimeString());
				console.log(evt);
				rt.removeStream(evt.uid);
			});

			rt.client.on("stream-subscribed", function(evt) {
				let stream = evt.stream;
				console.log("Got stream-subscribed event");
				console.log(new Date().toLocaleTimeString());
				console.log("Subscribe remote stream successfully: " + stream.getId());
				console.log(evt);
				rt.addStream(stream);
				
			});

			rt.client.on("stream-removed", function(evt) {
				let stream = evt.stream;
				console.log("Stream removed: " + stream.getId());
				console.log(new Date().toLocaleTimeString());
				console.log(evt);
				rt.removeStream(stream.getId());
				// this.stopGetStastRemoteStream()
			});
		},

		removeStream(uid) {
			this.streamList.map((item, index) => {
				if (item.getId() === uid) {
					item.close();
					let element = document.querySelector("#ag-item-" + uid);
					if (element) {
						element.parentNode.removeChild(element);
					}
					let tempList = [...this.streamList];
					tempList.splice(index, 1);
					this.streamList = tempList;
				}
			});
		},

		addStream(stream, push = false) {
			let repeatition = this.streamList.some(item => {
				return item.getId() === stream.getId();
			});
			if (repeatition) {
				return;
			}
			if (push) {
				this.streamList = this.streamList.concat([stream]);
			} else {
				this.streamList = [stream].concat(this.streamList);
			}
		},

		handleCamera(e) {
			e.currentTarget.classList.toggle("off");
			this.localStream.isVideoOn()
				? this.localStream.muteVideo()
				: this.localStream.unmuteVideo();
		},

		handleMic(e) {
			e.currentTarget.classList.toggle("off");
			this.localStream.isAudioOn()
				? this.localStream.muteAudio()
				: this.localStream.unmuteAudio();
		},

		switchDisplay(e) {
			if (
				e.currentTarget.classList.contains("disabled") ||
				this.streamList.length <= 1
			) {
				return;
			}
			if (this.displayMode === "pip") {
				this.displayMode = "tile";
			} else if (this.displayMode === "tile") {
				let hidestatsRemote = document.querySelector(".BxRemoteStats")
				hidestatsRemote.remove()
				this.displayMode = "pip";
			} else if (this.displayMode === "share") {
				
			} else {
				console.error("Display Mode can only be tile/pip/share");
			}
		},

		hideRemote(e) {
			if ( e.currentTarget.classList.contains("disabled") || this.streamList.length <= 1) {
				return;
			}
			let list;
			let id = this.streamList[this.streamList.length - 1].getId();
			let id2 = this.streamList[this.streamList.length - 2].getId();
			

			if(this.displayMode === "pip" && this.streamList.length === 3){
				list = Array.from(
					document.querySelectorAll(`#ag-item-${id}, #ag-item-${id2}`)
				);
				list.map(item => {
					if (item.style.display !== "none") {
						item.style.display = "none";
					} else {
						item.style.display = "block";
					}
				});
			}else if (this.displayMode === "pip" && this.streamList.length === 2){
				list = Array.from(
					document.querySelectorAll(`.ag-item:not(#ag-item-${id2})`)
				);
				list.map(item => {
					if (item.style.display !== "none") {
						item.style.display = "none";
					} else {
						item.style.display = "block";
					}
				});
			}else{
				let id3 = this.streamList[this.streamList.length - 3].getId();
				list = Array.from(
					document.querySelectorAll(`#ag-item-${id2}, #ag-item-${id3}`)
				);
				list.map(item => {
					if (item.style.display !== "none") {
						item.style.display = "none";
					} else {
						item.style.display = "block";
					}
				});
			}
		},

		handleExit(e) {
			
			if (e.currentTarget.classList.contains("disabled")) {
				return;
			}
			try {
				this.client && this.client.unpublish(this.localStream);
				this.localStream && this.localStream.close();
				this.client &&
				this.client.leave(
					() => {
						console.log("Client succeed to leave.");
						this.stopGetStastLocalStream()

					},
					() => {
						console.log("Client failed to leave.");
					}
				);
			} finally {
				this.readyState = false;
				this.client = null;
				this.localStream = null;
				
				// redirect to index
				this.$router.push("/");
			}
		},
		
		getStastLocalStream(){
			this.intervalLocal = setInterval(() => {
				let cnsLocal = document.querySelector("#stats" + this.streamList[0].getId());
				let Uid = this.localStream.getId()
				this.localStream.getStats((stats) => {
					//console.log(`Uid ${$.localStream.getId()} SDN access delay: ${stats.accessDelay} , Video send: ${stats.videoSendFrameRate} fps,  ${stats.videoSendResolutionWidth} X ${stats.videoSendResolutionHeight}`);
					cnsLocal.innerHTML = `
						<span> Uid : ${Uid}</span>
						<span> SDN Access Delay : ${stats.accessDelay} ms</span>
						<span> Video Send : ${stats.videoSendFrameRate} fps</span>
					`;
				});
				
			},1000)

			
		},

		stopGetStastLocalStream(){
 			clearInterval(this.intervalLocal)
		},


		getStastRemoteStream(item){
			this.intevalRemote = setInterval(() => {
				let cnsLocal = document.querySelector("#statsRemote" + this.streamList[0].getId());
				let Uid = this.streamList[0].getId()
				item.getStats((stats) => {
					//console.log(`Uid ${$.localStream.getId()} SDN access delay: ${stats.accessDelay} , Video send: ${stats.videoSendFrameRate} fps,  ${stats.videoSendResolutionWidth} X ${stats.videoSendResolutionHeight}`);
					cnsLocal.innerHTML = `
						<span> Uid : ${Uid}</span>
						<span> SDN Access Delay : ${stats.accessDelay} ms</span>
						<span> Video recv : ${stats.videoReceiveFrameRate} fps</span>
						<span> End to end delay : ${stats.endToEndDelay} ms</span>
					`;
				});
				
			},1000)
		},

		stopGetStastRemoteStream(){
 			clearInterval(this.intervalRemote)
		},

		streamInitShare(uid, attendeeMode, videoProfile, config) {
            let defaultConfig = {
               streamID: 'SHARES',
               audio: false,
               video: false,
               screen: true,
               mediaSource: 'window'
            };

            let stream = AgoraRTC.createStream(merge(defaultConfig, config));
            return stream;
		},

		handleScrenShare(){
			
			var uid;
			let $ = this;
		 	
			$.shareClient = AgoraRTC.createClient({ mode: 'rtc', codec: 'vp8'});
			$.shareClient.init($.appId, 
				() => {
					
					$.shareClient.join($.appId, $.channel, uid , uid => {
						$.shareStream = $.streamInitShare(uid, '720p_1');
						$.shareStream.init(
							() => {
								$.shareClient.publish($.shareStream, err => {
									console.log("Publish local stream error: " + err);
								});
								$.addStream($.shareStream, true);
								$.shareScreenStatus = true
								$.displayMode = "share"
								
							},
							err => {
								console.log("getUserMedia failed", err);
							}
						)

					})
				},
				err => {
					console.log("init Failed", err);
				}
			
			);
			

		},

		handleRemoveScrenShare(){
			this.shareClient && this.shareClient.unpublish(this.shareStream);
			this.shareStream && this.shareStream.close();
			this.shareClient && this.shareClient.leave(
					() => {
						console.log("Share Screen Success Remove.");
						this.displayMode ="pip"
						let BxLocalStats = document.querySelector(".BxLocalStats");
						let BxRemoteStats = document.querySelector(".BxRemoteStats");

						BxLocalStats.classList.remove("LocalStatsShare");
			
						BxRemoteStats.classList.remove("RemoteStatsShare");

					},
					() => {
						console.log("Share Screen failed to Remove.");
					}
				);
			
		},
		handleShowInfo(){
			this.showProfile = true
			let BxRemoteStats = document.querySelector(".BxRemoteStats");
			let BxLocalStats = document.querySelector(".BxLocalStats");

			if(BxLocalStats){
				BxLocalStats.classList.remove("HideInfodiv");
			}

			if(BxRemoteStats){
				BxRemoteStats.classList.remove("HideInfodiv");
			}

			
		

		},
		handleHideInfo(){
			this.showProfile = false
			let BxRemoteStats = document.querySelector(".BxRemoteStats");
			let BxLocalStats = document.querySelector(".BxLocalStats");

			if(BxLocalStats){
				BxLocalStats.classList.add("HideInfodiv");
			}

			if(BxRemoteStats){
				BxRemoteStats.classList.add("HideInfodiv");
			}
		},

		handleChat(){
			let WindowChat = document.querySelector("#WindowChat");
			if(WindowChat.classList.contains("hideWindow")){
				WindowChat.classList.remove("hideWindow")
			}else{
				WindowChat.classList.add("hideWindow")
			}
		}

	},

	created() {
		AgoraRTC.Logger.setLogLevel(AgoraRTC.Logger.INFO);
		AgoraRTC.Logger.enableLogUpload();
		AgoraRTC.getDevices (function(devices) {
			var devCount = devices.length;
			var id = devices[0].deviceId;
			console.log(devices)
		}, function(errStr){
				console.error("Failed to getDevice", errStr);
		});
		navigator.mediaDevices.getUserMedia({video: true, audio: true})
		.then(function(mediaStream){
			return AgoraRTC.getSupportedCodec({stream: mediaStream});
		})
		.then(function(result){
			console.log(`Supported video codec: ${result.video.join(",")}`);
			console.log(`Supported audio codec: ${result.audio.join(",")}`);
		});

		AgoraRTC.getDevices(function(devices){
				var audioDevices = devices.filter(function(device){
        			return device.kind === "audioinput";
				});

    			var videoDevices = devices.filter(function(device){
        			return device.kind === "videoinput";
				});
				
				Cookies.set("camera", videoDevices[0].label);
				Cookies.set("audio", audioDevices[0].label);
			
			})

		let $ = this;
		// init AgoraRTC local client
		$.client = AgoraRTC.createClient({ mode: $.transcode });
		$.client.init($.appId, () => {
			console.log("AgoraRTC client initialized");
			$.subscribeStreamEvents();
			$.client.join($.appId, $.channel, $.uid, uid => {
				var datejoin = new Date().toLocaleTimeString('en-US', { hour12: false,hour: "numeric",minute: "numeric", second:"numeric"})
				console.log("User " + uid + " join channel successfully" + " At "+ datejoin);
				$.client.enableDualStream(function() {
					console.log("Enable dual stream success!")
				}, function(err) {
					console.log(err)
				});

				
				// create local stream
				// It is not recommended to setState in function addStream
				$.localStream = this.streamInit(uid, $.attendeeMode, $.videoProfile);
				$.localStream.init(
					() => {
						if ($.attendeeMode !== "audience") {
							$.addStream($.localStream, true);
							
							$.client.publish($.localStream, err => {
								console.log("Publish local stream error: " + err);
							});
						}
						$.readyState = true;
						this.getStastLocalStream()
					},
					err => {
						console.log("getUserMedia failed", err);
						$.readyState = true;
					}
				);
				$.localStream.on("accessDenied", function() {
					console.log("accessDenied");
				});
						
			});
		});
	},

	mounted() {
		this.$nextTick(() => {
			// add listener to control btn group
			let canvas = document.querySelector("#ag-canvas");
			let btnGroup = document.querySelector(".ag-btn-group");
			canvas.addEventListener("mousemove", () => {
				if (global._toolbarToggle) {
				clearTimeout(global._toolbarToggle);
				}
				btnGroup.classList.add("active");
				global._toolbarToggle = setTimeout(function() {
				btnGroup.classList.remove("active");
				}, 2000);
			});
		});
	},

	beforeUpdate(e) {
		let $ = this;
		// rerendering
		let canvas = document.querySelector("#ag-canvas");
		// pip mode (can only use when less than 4 people in channel)
		if ($.displayMode === "pip") {
			let no = $.streamList.length;
			if (no > 4) {
				$.displayMode = "tile";
				return;
			}

			if (no === 3){
				$.streamList.map((item, index) => {
					let id = item.getId();
					let dom = document.querySelector("#ag-item-" + id);
					let BxRemoteStats = document.querySelector(".BxRemoteStats");
					let BxLocalStats = document.querySelector(".BxLocalStats");

					if (!dom) {
						dom = document.createElement("section");
						dom.setAttribute("id", "ag-item-" + id);
						dom.setAttribute("class", "ag-item");
						canvas.appendChild(dom);
						item.play("ag-item-" + id);
					}
					if (index === no - 1) {
						dom.setAttribute(
							"style",
							`grid-area: span 2/span 3/9/25;
							z-index:1;width:calc(100% - 20px);height:calc(100% - 20px);position:relative;border:1px solid green;`
						);
						let canvasVideo = document.querySelector("#ag-item-" + id);
						let div = document.createElement('div');
						div.id = 'stats'+$.streamList[0].getId();
						div.className= 'BxLocalStats HideInfodiv'
						canvasVideo.appendChild(div);
						BxRemoteStats.remove()
						
					} else if (index === no - 2){
						dom.setAttribute(
							"style",
							`grid-area: span 2/span 3/11/25;
							z-index:1;width:calc(100% - 20px);height:calc(100% - 20px);position:relative;border:1px solid red;`
						);

						let canvasVideo = document.querySelector("#ag-item-" +id);
						let div = document.createElement('div');
						div.id = 'statsRemote'+$.streamList[0].getId();
						div.className= 'BxRemoteStats HideInfodiv'
						div.classList.add("RemoteStatsShare")
						canvasVideo.appendChild(div);
						BxLocalStats.remove()

						$.getStastRemoteStream(item)
						
					}else if (index === no - 3){
						dom.setAttribute("style", `grid-area: span 12/span 24/13/25`);
						let domVideo = document.querySelector("#video" + $.streamList[0].getId());
						domVideo.setAttribute(
							"style",
							`width: 100%;height: 100%;position: absolute;object-fit: contain;`
						);
					}
					item.player.resize && item.player.resize();
					
				});
				
			}else{
				let BxRemoteStats = document.querySelector(".RemoteStatsShare");
				if(BxRemoteStats){
					BxRemoteStats.remove()
				}

				$.streamList.map((item, index) => {
					let id = item.getId();
					
					
					let dom = document.querySelector("#ag-item-" + id);
					if (!dom) {
						dom = document.createElement("section");
						dom.setAttribute("id", "ag-item-" + id);
						dom.setAttribute("class", "ag-item");
						canvas.appendChild(dom);
						item.play("ag-item-" + id);
					}
					if (index === no - 1) {
						dom.setAttribute(
							"style",
							`grid-area: span 3/span 4/4/25;
							z-index:1;width:calc(100% - 20px);height:calc(100% - 20px);position:relative;`
						);
						
						let canvasVideo = document.querySelector("#ag-item-" + id);
						let div = document.createElement('div');
						div.id = 'stats'+$.streamList[0].getId();
						div.className= 'BxLocalStats HideInfodiv';
						canvasVideo.appendChild(div);

						

					}else if(index === no - 2){
						

						dom.setAttribute("style", `grid-area: span 12/span 24/13/25`);
						let canvasVideo = document.querySelector("#ag-item-" +id);
						let div = document.createElement('div');
						div.id = 'statsRemote'+$.streamList[0].getId();
						div.className= 'BxRemoteStats HideInfodiv'
						canvasVideo.appendChild(div);

						$.getStastRemoteStream(item)

						let hide = document.querySelector(".BxLocalStats")
						hide.remove()

					}
					 else {
						dom.setAttribute(
							"style",
							`grid-area: span 3/span 4/${4 + 3 * index}/25;
							z-index:1;width:calc(100% - 20px);height:calc(100% - 20px)`
						);
					}
					item.player.resize && item.player.resize();

				});
			}
			
		} else if ($.displayMode === "tile") {
		// tile mode
			let no = $.streamList.length;
			$.streamList.map((item, index) => {
				let id = item.getId();
				let dom = document.querySelector("#ag-item-" + id);
				if (!dom) {
					dom = document.createElement("section");
					dom.setAttribute("id", "ag-item-" + id);
					dom.setAttribute("class", "ag-item");
					canvas.appendChild(dom);
					item.play("ag-item-" + id);
				}
				dom.setAttribute("style", `grid-area: ${tile_canvas[no][index]};position:relative;`);
				
				let BxLocalStats = document.querySelector(".BxLocalStats");
				BxLocalStats.setAttribute(
					"style",
					`top: 10px;left: 10px;width: auto;font-size: .625rem;border-radius: 4px;height: fit-content;border-top-right-radius: 4px;`
				);
				
				item.player.resize && item.player.resize();
			});
		} else if ($.displayMode === "share") {
			let no = $.streamList.length;
			$.streamList.map((item, index) => {
				let id = item.getId();
				let dom = document.querySelector("#ag-item-" + id);
				if (!dom) {
					dom = document.createElement("section");
					dom.setAttribute("id", "ag-item-" + id);
					dom.setAttribute("class", "ag-item");
					canvas.appendChild(dom);
					item.play("ag-item-" + id);
				}

				if (index === no - 1) {
					dom.setAttribute("style", `grid-area: span 12/span 24/13/25`);
				} else if(index === no - 2){
					dom.setAttribute(
						"style",
						`grid-area: span 2/span 3/9/25;
						z-index:1;width:calc(100% - 20px);height:calc(100% - 20px);position:relative;`
					);
					let BxRemoteStats = document.querySelector(".BxRemoteStats");
					BxRemoteStats.classList.add("RemoteStatsShare")
				} else if(index === no - 3){
					dom.setAttribute(
						"style",
						`grid-area: span 2/span 3/11/25;
						z-index:1;width:calc(100% - 20px);height:calc(100% - 20px);position:relative;`
					);
					let BxLocalStats = document.querySelector(".BxLocalStats");
					BxLocalStats.classList.add("LocalStatsShare");
				}
				item.player.resize && item.player.resize();

			});
		}
	},

	beforeDestroy () {
		this.client && this.client.unpublish(this.localStream);
		this.localStream && this.localStream.close();
		this.client &&
		this.client.leave(
			() => {
				console.log("Client succeed to leave.");
			},
			() => {
				console.log("Client failed to leave.");
			}
		);

	}
};
</script>

<style>
#ag-canvas {
  height: 100%;
  display: grid;
  grid-gap: 10px;
  align-items: center;
  justify-items: center;
  grid-template-rows: repeat(12, auto);
  grid-template-columns: repeat(24, auto);
}

.ag-item :first-child {
  border-radius: 6px;
}
.ag-item {
  border-radius: 6px;
  border: 1px #00b6ed solid;
  background: url("../assets/images/avatar.png") center no-repeat;
  height: 100%;
  width: 100%;
}

#ag-resize-container {
  background-image: none !important;
  background-color: black;
  display: flex;
  justify-content: center;
  height: center;
}

#ag-resize-container .ag-item {
  border: none !important;
}

/* button group */
.ag-btn-group {
  height: 70px;
  width: calc(100% - 60px);
  margin: 0 auto;
  position: absolute;
  bottom: 0;
  background: transparent;
  z-index: 11;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.ag-btn-group:hover,
.ag-btn-group.active {
  background: rgba(18, 74, 99, 0.8);
}

.ag-btn {
  z-index: 12;
  opacity: 0;
  color: white;
  /* width: 35px;
    height: 35px; */
  cursor: pointer;
  font-size: 60px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}
.ag-btn:hover {
  color: #00b6ed;
}

.ag-btn.disabled,
.ag-btn.disabled:hover {
  color: #637c8e;
  cursor: not-allowed;
}

.ag-btn-group:hover .ag-btn,
.ag-btn-group.active .ag-btn {
  opacity: 1;
}
.ag-btn.off.videoControlBtn .ag-icon-camera {
  display: none;
}
.ag-btn.videoControlBtn .ag-icon-camera-off {
  display: none;
}
.ag-btn.videoControlBtn .ag-icon-camera {
  display: inline-block;
}
.ag-btn.off.videoControlBtn .ag-icon-camera-off {
  display: inline-block;
}
.ag-btn.off.audioControlBtn .ag-icon-mic {
  display: none;
}
.ag-btn.audioControlBtn .ag-icon-mic-off {
  display: none;
}
.ag-btn.audioControlBtn .ag-icon-mic {
  display: inline-block;
}
.ag-btn.off.audioControlBtn .ag-icon-mic-off {
  display: inline-block;
}
.ShowInfo, .HideInfo{
	font-weight: 600;
	font-size: 40px;
}
.HideInfo{
	color: #00b6ed;
}
.HideInfodiv{
	display: none !important;
}
.ChatWindow{
	font-size: 40px;
}
.hideWindow{
	display: none;
}

video {
  max-width: unset !important;
}
.BxLocalStats, .BxRemoteStats{
	position: absolute;
	color: #fff;
    display: flex;
	flex-direction: column;
	padding: 5px;
	background-color: rgba(0, 0, 0, 0.522);
	
}
.BxLocalStats{
    bottom: 0px;
	left: 0px;
	width: 100%;
	font-size: .525rem;
	border-radius: 4px;
	border-top-right-radius: 0px !important;
}

.BxRemoteStats {
	top: 5px;
    left: 35px;
    font-size: .625rem;
    border-radius: 4px;

}

.LocalStatsShare,.RemoteStatsShare{
	top: initial;
	bottom: 0px;
	left: 0px;
	width: auto;
	font-size: .425rem;
	border-radius: 4px;
	height: fit-content;
}

</style>




