<template>
	<div class="wrapper meeting">
		<div class="ag-header">
			<div class="ag-header-lead">
				<img class="header-logo" :src="require('../assets/images/ag-logo.png')" alt="" />
				<span>AgoraWeb RTCSDK-3.0.2</span>
			</div>
			<div class="ag-header-msg">
				<div class="ag-header-div">ROOM:&nbsp;<span id="room-name">{{channel}}</span></div>
				<div class="ag-header-div">VIDEO INPUT:&nbsp;<span id="room-name">{{camera}}</span></div>
				<div class="ag-header-div">AUDIO INPUT:&nbsp;<span id="room-name">{{audio}}</span></div>
			</div>
		</div>
		<div class="ag-main">
			<div class="ag-container">
				<AgoraVideoCall 
					:videoProfile="videoProfile"
					:channel="channel"
					:transcode="transcode"
					:attendeeMode="attendeeMode"
					:baseMode="baseMode"
					:appId="appId"
					:uid="uid"
				>
				</AgoraVideoCall>
			</div>
			<!-- <div class="ag-chat">
				<AgoraChat/>
			</div> -->

			<div class="BxLive__BxRight hideWindow" id="WindowChat">
				<div class="BxLive__BxRight--BxTab">
					<div class="tabTitle">
					
					</div>
					<div class="tabContainer">
						<AgoraChat />
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import * as Cookies from "js-cookie";
	import AgoraVideoCall from "@/components/AgoraVideoCall";
	import AgoraChat from "@/components/QuickChat";
	import {AGORA_APP_ID} from "@/agora.config"
	export default {
		components: {
			AgoraVideoCall,
			AgoraChat
		},
		data() {
			return {
				videoProfile: Cookies.get("videoProfile").split(",")[0] || "480p_4",
				channel: Cookies.get("channel") || "test",
				transcode: Cookies.get("transcode") || "rtc",
				attendeeMode: Cookies.get("attendeeMode") || "video",
				baseMode: Cookies.get("baseMode") || "avc",
				uid: undefined,
				camera: Cookies.get("camera"),
				audio : Cookies.get("audio"),
			};
		},

		created() {
			this.appId = AGORA_APP_ID
			if (!this.appId) {
				return alert('Get App ID first!')
			}

		}
	};
</script>

<style scoped>
.meeting.wrapper {
  background: rgb(12, 43, 64);
  height: 100%;
}

.meeting .ag-header {
  padding: 20px 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.ag-header-lead {
  font-size: 16px;
  line-height: 35px;
}

.meeting .ag-footer {
  padding: 0 30px;
  display: flex;
  align-items: center;
}

.meeting .ag-main {
  position: relative;
}

.meeting .ag-footer :first-child {
  margin-right: 80px;
}

.header-logo {
  width: 60px;
  height: 35px;
  margin-right: 12px;
}

.ag-container {
  width: calc(100% - 60px);
  height: 100%;
  margin: 0 auto;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 12px;
}

.ag-header-msg{
	font-size: .725rem;
	font-weight: 700;
}

.ag-header-msg > .ag-header-div > #room-name{
	font-weight: 500;

}

.ag-ShareScreen{
	border: 1px solid red;
}

.ag-chat{
	z-index: 2;
	position:absolute;
	top: 0;
	background: rgb(12, 43, 64);
	width: 300px;
	min-height: 400px;
}


</style>




