import Vue from 'vue'
import Router from 'vue-router'
import Home from './Home'
import Meeting from './Meeting'
import Landing from './Landing.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: '/prodwebdemoagora/',
  routes: [
    {
      path: '/',
      name: 'Landing',
      component: Landing
    },
    {
      path: '/home',
      name: 'Agora.Home',
      component: Home
    },
    {
      path: '/meeting',
      name: 'Meeting',
      component: Meeting
    }
  ]
})



