<template>
    <div class="BxLandingPage">
        <div class="Title">
            Please Choose Demo
        </div>

        <div class="bXbUTTON">
            <div class="btnAgora" @click="HandleAgora">Agora</div>
            <a class="btnOpentook" href="https://opentok.neotebet.de/">Opentok</a>
        </div>
    </div>
</template>

<script>
    export default {
        methods: {
            HandleAgora(){
                this.$router.push('/home').catch(err => {
                    console.log(err)
                })
            }
        },
    }
</script>

<style scoped>
    .BxLandingPage{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }

    .Title{
        font-size: 2rem;
    }

    .bXbUTTON{
        display: flex;
        margin-top: 20px;
        gap: 20px;
    }

    .btnAgora {background-image: linear-gradient(to right, #4776E6 0%, #8E54E9  51%, #4776E6  100%)}
    .btnAgora {
        margin: 10px;
        padding: 15px 45px;
        text-align: center;
        text-transform: uppercase;
        transition: 0.5s;
        background-size: 200% auto;
        color: white;            
        box-shadow: 0 0 20px #eee;
        border-radius: 4px;
        display: block;
        cursor: pointer;
        font-weight: 600;
    }

    .btnAgora:hover {
        background-position: right center; /* change the direction of the change here */
        color: #fff;
        text-decoration: none;
    }


    .btnOpentook {background-image: linear-gradient(to right, #4CB8C4 0%, #3CD3AD  51%, #4CB8C4  100%)}
    .btnOpentook {
        margin: 10px;
        padding: 15px 45px;
        text-align: center;
        text-transform: uppercase;
        transition: 0.5s;
        background-size: 200% auto;
        color: white;            
        box-shadow: 0 0 20px #eee;
        border-radius: 4px;
        display: block;
        cursor: pointer;
         font-weight: 600;
    }

    .btnOpentook:hover {
        background-position: right center; /* change the direction of the change here */
        color: #fff;
        text-decoration: none;
    }
         
         
</style>




